saveDatas.forEach(function (region) {
  //分别筛选出省/市/区的数据
  if (region.code.slice(-4) == '0000') {
    //省
    province.push(region);
  } else if (region.code.slice(-4) != '0000' && region.code.slice(-2) == '00') {
    //获取市的数据
    //自治州太长了，去掉自治州
    if (region.name.indexOf('自治州') != -1) {
      region.name = region.name.replace('自治州', '');
    }
    city.push(region);
  } else {
    //获取县的数据
    area.push(region);
  }
});
//返回出的数据
datas = {
  province: province,
  city: city,
  area: area
};
exports.default = datas;

